#unordered{
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
  }
  
.item {
    float: left;
  }
  
.item a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }
  
.item a:hover {
    background-color: #111;
  }

#logo img{
    width: 80px;
    height: 50px;
}